/* eslint-disable no-undef */
// eslint-disable-next-line import/no-unresolved
import Choices from 'choices.js';

Drupal.behaviors.choices = {
  attach(context) {
    if (document.querySelector('.form_item__select', context)) {
      Array.from(document.querySelectorAll('.form_item__select')).forEach(
        (element) =>
          new Choices(element, {
            searchEnabled: false,
            placeholder: true,
            placeHolderValue: 'Filter Selections',
            shouldSort: false,
            callbackOnCreateTemplates(template) {
              return {
                item: ({ classNames }, data) => {
                  return template(`
                    <div class="${classNames.item} ${
                    data.highlighted
                      ? classNames.highlightedState
                      : classNames.itemSelectable
                  } ${
                    data.placeholder ? classNames.placeholder : ''
                  }" data-item data-id="${data.id}" data-value="${
                    data.value
                  }" ${data.disabled ? 'aria-disabled="true"' : ''}>
                  ${data.label}
                  </div>
               `);
                },
                // Hardcoding the data-type to fix ARIA issues, removed roles to fix ARIA issue
                containerOuter: ({ classNames }) => {
                  return template(`
                  <div class="${classNames.containerOuter}"
                  aria-haspopup="true" data-type="select-one" tabindex="0">
                      </div>
                    `);
                },
              };
            },
          }),
      );
    }
  },
};
